@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
html,body
{
  width: 100%;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden; 
}

/* NAVBAR */
.logo-img {
  height: 50px;
  width: 60px;
}

.intro-img {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  bottom: 280;
  right: 0;
  width: 70px;
  height: 70px;
  margin: 70px auto 0;
  justify-content: center;
  align-items: center;
}

/* ABOUT */
.card-img {
  height: 250px;
  width: 50px;
}

.title {
  text-align: center;
  margin: 20px;
  font-size: 2rem;
}

.centered {
  text-align: center;
  margin: 70px auto;
}

.intro {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.7)) , url(/static/media/IMG-20211024-WA0043.efd2c033.jpg);
  background-position: center;
  background-size: cover;
  height: 550px;
  margin-top: 0px;
  background-repeat: no-repeat;
}

.our-classes {
  text-decoration: none;
  color: black;
}

.our-classes:hover {
  color: black;
  opacity: .9;
}

.testimonials {
  background-image: linear-gradient(rgba(0, 0, 0, 0.95),rgba(0, 0, 0, 0.7)) , url(/static/media/IMG-20211024-WA0036.75a70d94.jpg);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}


.underline {
  text-decoration: underline;
}


/* NAVBAR DROPDOWN */

.dropdown:hover>.dropdown-menu {
  display: block;
}

.dropdown>.dropdown-toggle:active {
  /*Without this, clicking will make it sticky*/
    pointer-events: none;
}

/* Admissions */
.admission-process {
  background-color: blue;
background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(7, 10, 102, 0.8)) , url("https://www.transparenttextures.com/patterns/3px-tile.png");
}

.steps-to-apply {
  background-color: white;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  text-align: center;
  font-size: 30px;
  color: rgba(7, 11, 139, 0.8);
}


/* PORTAL */
.portal-background {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.9)) , url(/static/media/IMG-20211024-WA0044.6838be95.jpg);
  background-position: center;
  background-size: cover;
  height: 587px;
  text-align: center;
  /* background-attachment: fixed; */
}

.portal-center {
  margin: 0 auto 0;
  width: 290px;
}

.objective-list li {
    font-size: 16px;
    margin-left: 10px;
    list-style-type: circle;
    line-height: 2;
 }

 .reviews {
   font-size: 20px;
   font-weight: lighter;
 }


/* FOOTER */
.footer-link {
  text-decoration: none;
  color: white;
}

.footer-link:hover {
  color: #FFD523;
}


/* CLASSES  */
.creche-header-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.7)) , url(/static/media/IMG-20211026-WA0002.4bcf1d38.jpg);
  background-position: center;
  background-size: cover;
  height: 500px;
}

.nursery-header-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.7)) , url(/static/media/IMG-20211026-WA0012.21b2848b.jpg);
  background-position: center;
  background-size: cover;
  height: 500px;
}

.primary-header-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.7)) , url(/static/media/IMG-20211024-WA0043.efd2c033.jpg);
  background-position: center;
  background-size: cover;
  height: 500px;
}

.secondary-header-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.7)) , url(/static/media/IMG-20211024-WA0045.3274ecbf.jpg);
  background-position: center;
  background-size: cover;
  height: 500px;
}

/* ANTHEM */
.main-anthem {
  margin: -30px auto;
  width: 500px;
  box-shadow: 1px 0px 3px black;
  background-color: white;
  padding: 15px;
}

/* ABOUT US */
.background {
  margin: 50px 20vw;
}

.core-values {
  border-radius: 50%;
  width: 300px;
  height: 300px;
  background-color: #FFD523;
  position: relative;
  margin: 20px auto;
  box-shadow: 7px 5px 1px #161E54;
}

.main-values {
  position: absolute;
  left: -5px;
  top: 20px;
  right: 10px;
  margin: 10px 5px;
}

.main-values li {
  list-style-type: none;
  display: inline-block;
  padding: 5px;
  font-weight: bold;
}

/* CONTACT */
.main-contact {
  margin: -35px auto 50px;
  background-color: white;
  padding: 20px;
  height: 500px;
  width: 600px;
  box-shadow: 1px 0px 3px #161E54;
}

.contact-form {
  margin: 30px auto;
  width: 500px;
}

/* MEDIA QUERIES */
@media (min-width: 300px) and (max-width: 700px) {
  .centered {
    align-items: center;
    margin: 70px 20px;
  }
}

@media (max-width: 780px) {
  .intro-text {
    font-size: 150%;
  }
}


.main-dropdown-menu {
  background: #064635;
  width: 120px;
  position: absolute;
  top: 57px;
  list-style: none;
  text-align: start;
}

.main-dropdown-menu li {
  background: #161E54;
  margin: 0px -35px;
  cursor: pointer;
}

.main-dropdown-menu li:hover {
  /* background: #051367; */
  text-decoration: underline;
}

.main-dropdown-menu.clicked {
  display: none;
}

.main-dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 14px;
}

.main-dropdown-link:hover {
  color: #fff;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: none;
  }
}

.main-navbar {
  background: #064635;
  /* height: 80px; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}

.logo-img {
  margin: 10px 0 5px -8px;
}

.logo-text {
  width: 90px;
  float: left;
  margin: 25px 0 0 -7px;
  color: #fff;
}

.church-logo {
  font-size: 10px;
  margin-top: -15px;
  width: 120px;
}

.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.main-nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-top: 1rem;
}

.main-nav-item {
  display: flex;
  align-items: center;
}

.main-nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.main-nav-links:hover {
  background-color: #051367;
  color: #fff;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.main-menu-icon {
  display: none;
}

